import * as React from 'react';
import { cva } from 'class-variance-authority';

import { cn } from '@/lib/utils';

const circularProgressVariants = cva('relative text-primary');

export type CircularProgressProps = React.ComponentProps<'div'> & {
  /**
   * The value of the progress indicator for the determinate variant.
   * Value between 0 and 100.
   */
  value?: number;
  /**
   * The thickness of the progress ring.
   */
  thickness?: number;
  /**
   * If true, the circular progress is indeterminate.
   * @default true
   */
  indeterminate?: boolean;
  /**
   * If true, shows the progress value as a label in the center of the circle.
   * @default false
   */
  showLabel?: boolean;
  /**
   * Additional CSS classes to apply to the label.
   */
  labelClassName?: string;
  /**
   * Custom render function for the label. Receives the current value.
   * If not provided, displays the value as a percentage.
   */
  renderLabel?: (value: number) => React.ReactNode;
  /**
   * The size of the circular progress indicator.
   * @default 'md'
   */
  size?: 'sm' | 'md' | 'lg';
};

function CircularProgress({
  className,
  size = 'md',
  value = 0,
  thickness = 6,
  indeterminate = true,
  showLabel = false,
  labelClassName,
  renderLabel,
  ...props
}: CircularProgressProps) {
  const normalizedValue = Math.min(Math.max(value, 0), 100);

  // Calculate size-based dimensions
  const sizeMap = {
    sm: 24,
    md: 40,
    lg: 64,
  };

  const pixelSize = sizeMap[size];
  const radius = (pixelSize - thickness) / 2;
  const circumference = 2 * Math.PI * radius;
  const strokeDashoffset =
    circumference - (normalizedValue / 100) * circumference;

  const viewBox = `0 0 ${pixelSize} ${pixelSize}`;

  return (
    <div
      data-slot="circular-progress"
      className={cn(circularProgressVariants(), className)}
      style={{ width: pixelSize, height: pixelSize }}
      role="progressbar"
      aria-valuemin={0}
      aria-valuemax={100}
      aria-valuenow={indeterminate ? undefined : normalizedValue}
      {...props}
    >
      <svg
        width={pixelSize}
        height={pixelSize}
        viewBox={viewBox}
        style={{ transform: 'rotate(-90deg)' }}
        className="relative"
      >
        {/* Background circle */}
        <circle
          r={radius}
          cx={pixelSize / 2}
          cy={pixelSize / 2}
          fill="transparent"
          strokeWidth={thickness}
          strokeDasharray={circumference}
          strokeDashoffset="0"
          className={cn('stroke-current opacity-20')}
        />
        {/* Progress circle */}
        <circle
          r={radius}
          cx={pixelSize / 2}
          cy={pixelSize / 2}
          strokeWidth={thickness}
          strokeLinecap="round"
          strokeDasharray={circumference}
          strokeDashoffset={
            indeterminate ? circumference * 0.75 : strokeDashoffset
          }
          fill="transparent"
          className={cn(
            'stroke-current',
            indeterminate
              ? 'circular-progress-indeterminate'
              : 'transition-all duration-300 ease-in-out'
          )}
        />
      </svg>
      {showLabel && !indeterminate && (
        <div
          className={cn(
            'absolute inset-0 flex items-center justify-center text-sm font-medium',
            labelClassName
          )}
        >
          {renderLabel ? renderLabel(normalizedValue) : `${normalizedValue}%`}
        </div>
      )}
    </div>
  );
}

export { CircularProgress, circularProgressVariants };
